<template>
    <!--    <div class="view-wrapper">-->
    <div class="view-wrapper" :style="{height:pageMixin_browser.clientHeight}">
        <DP_Popup
                :display.sync="popup.show"
                :show-close="true"
                background-color="white"
                :title="popup.title"
        >
            <img class="qrcode" :src="pageQrcode_img" alt="">
            <div>请打开微信扫一扫</div>
        </DP_Popup>
        <div class="content">
            <!--            <img ref="qrcode" width="150px" v-show="qrcode" crossorigin="anonymous"-->
            <!--                 :src="qrcode"-->
            <!--            />-->
            <img v-show="parameter.avatar" ref="avatar" class="avatar" width="100px" crossorigin="anonymous"
                 :src="parameter.avatar"
                 alt="">
            <template v-show="showImg">
                <div class="poster-wrapper" ref="posterWrapper">
                    <img ref="poster_output" class="poster_output" v-show="output_img" :src="output_img" alt="">
                    <img ref="poster" v-show="!output_img" class="origin"
                         crossorigin="anonymous"
                         :src="parameter.poster"
                         alt="">
                    <canvas id="canvas" ref="canvas"></canvas>
                </div>
            </template>

            <div class="in-development" v-if="output_img===''">{{tips}}</div>

        </div>

        <div class="share-in-app" v-if="pageMixin_browser.device.isDPApp && output_img">
            <div class="option-wrapper">
                <a class="option" :href="'uniwebview://wechatsharetofriend?image='+output_img">
                    <div class="icon friend"></div>
                    <div class="label">微信</div>
                </a>
                <a class="option" :href="'uniwebview://wechatsharetomoments?image='+output_img">
                    <div class="icon moment"></div>
                    <div class="label">朋友圈</div>
                </a>
                <a class="option">
                    <div class="icon scan" @click="openPopup"></div>
                    <div class="label">扫码</div>
                </a>
            </div>

        </div>
        <div class="share-in-wechat" v-if="pageMixin_browser.device.isWechat && output_img">
            <div class="arrow"></div>
            <div class="tips">长按上图保存到相册，分享给好友</div>

        </div>

    </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import {getPageUrlQrCode, getQrCode} from "@/api/parameter";
    import {mapGetters} from "vuex";
    // import {getLocalData, setLocalData} from "@/utils/function";
    import DP_Popup from '../../components/DP_Popup'

    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title || '熊猫博士', // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content: this.$route.meta.title + ",熊猫博士"
                }],
            }
        },
        name: "index",
        components: {DP_Popup},
        mixins: [pageMixin],
        props: {},
        data() {
            return {
                parameter: {
                    user_id: '',
                    bundleId: '',
                    avatar: '',
                    sentence: '',
                    poster: '',
                    name: '',
                    // qrcode: '',
                    // pageQrcode: "",
                },
                tips: "海报正在生成...",
                pageQrcode_img: "",
                qrcodeObj: null,
                avatarObj: null,
                posterObj: null,
                wechatJump: '',
                posterData: [],
                showImg: false,
                output_img: '',
                p_img: false,
                a_img: false,
                popup: {
                    show: false,
                    title: "",
                }
            }
        },
        watch: {},
        computed: {
            ...mapGetters({
                pageParameter: "getPageParameter",
                userInfo: "getUserInfo",
                browserInfo: "getBrowser",
            }),
        },
        methods: {
            pageMixin_wechatJsSdkFn(wx, showButton = false) {

                wx.updateAppMessageShareData({
                    title: '熊猫博士送你爱心会员', // 分享标题
                    desc: '领取立即免费学习', // 分享描述
                    link: this.wechatJump, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://business.xiongmaoboshi.com/cms_media/200.png', // 分享图标
                    success: function () {
                        // 设置成功
                    }
                })

                wx.updateTimelineShareData({
                    title: '熊猫博士识字送你爱心会员', // 分享标题
                    link: this.wechatJump, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://business.xiongmaoboshi.com/cms_media/200.png', // 分享图标
                    success: function () {
                        // 设置成功
                    }
                });


                if (showButton) {
                    wx.showAllNonBaseMenuItem();
                    wx.hideMenuItems({
                        menuList: [
                            'menuItem:copyUrl',
                            'menuItem:originPage',
                            'menuItem:openWithQQBrowser',
                            'menuItem:share:brand"',
                            'menuItem:openWithSafari',
                            'menuItem:copyUrl',
                        ] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮，所有menu项见附录3
                    });

                } else {
                    wx.hideAllNonBaseMenuItem();
                }

            },
            openPopup() {

                this.popup.show = true;

            },
            async getAllImage() {
                // 在app 中 获取 页面二维码
                if (this.browserInfo.device.isDPApp) {
                    const pageQrcodeRes = await getPageUrlQrCode(this.parameter.bundleId, true);
                    this.pageQrcode_img = pageQrcodeRes.data.code === 0 ? 'data:image/jpeg;base64,' + pageQrcodeRes.data.data : '';
                }
                this.avatarObj = this.parameter.avatar ? await this.getImg('avatar') : null;
                this.posterObj = this.parameter.poster ? await this.getImg('poster') : null;
                const qrcodeRes = await getQrCode(this.parameter.user_id);
                this.qrcodeObj = new Image();
                this.qrcodeObj.src = qrcodeRes.data.code === 0 ? 'data:image/jpeg;base64,' + qrcodeRes.data.data.qrCodeBase64 : '';
                if (qrcodeRes.data.code === 0) {
                    this.wechatJump = qrcodeRes.data.data.contentUrl;
                    if (this.pageMixin_wechatJsSdk){
                        this.pageMixin_wechatJsSdkFn(this.pageMixin_wechatJsSdk,true);
                    }
                }
                this.qrcodeObj.onload = () => {
                    this.drawPoster();
                }
            },
            async drawPoster() {
                const {canvas} = this.$refs;
                const width = 640;
                const height = 1138;
                canvas.width = width;
                canvas.height = height;
                const ctx = canvas.getContext('2d');
                this.posterObj ? ctx.drawImage(this.posterObj, 0, 0, width, height) : console.log('无海报信息');
                ctx.fillStyle = "white";
                ctx.font = "22px Helvetica, Arial, sans-serif";
                this.parameter.name ? ctx.fillText(this.parameter.name, 130, 65) : console.log('无用户昵称');
                ctx.font = "18px Helvetica, Arial, sans-serif";
                this.parameter.sentence ?  ( this.parameter.name ? ctx.fillText(this.parameter.sentence, 130, 95) : ctx.fillText(this.parameter.sentence, 65, 65)  ): console.log('无分享语句');
                this.avatarObj ? ctx.drawImage(this.avatarObj, 30, 30, 80, 80) : console.log('无头像信息');
                ctx.drawImage(this.qrcodeObj, 430, 936, 150, 150);
                this.output_img = canvas.toDataURL();
            },
            getImg(ref) {
                const img = this.$refs[ref];
                return new Promise((resolve, reject) => {
                    if (img.complete) {
                        resolve(img);
                        console.log('图片加载已完成');
                    } else {
                        img.onload = () => {
                            resolve(img);
                        }
                        img.onerror = () => {
                            this.dp_Tips('提示', '海报加载错误 请重试')
                            reject('图片加载失败');
                        }
                        img.onabort = () => {
                            this.dp_Tips('提示', '海报加载失败 请重试')
                            reject('图片取消加载');
                        }


                    }

                })


            },
        },
        created() {

            // setTimeout(() => {
            //     const wx = this.pageMixin_jsSdk;
            //     if (wx) {
            //         wx.hideMenuItems({
            //             menuList: [
            //                 'menuItem:share:appMessage',
            //                 'menuItem:share:timeline',
            //                 'menuItem:share:qq',
            //                 'menuItem:share:weiboApp',
            //                 'menuItem:share:favorite',
            //                 'menuItem:share:facebook',
            //                 'menuItem:share:QZone',
            //                 "menuItem:favorite",
            //                 'menuItem:copyUrl',
            //                 "menuItem:openWithQQBrowser",
            //                 "menuItem:openWithSafari",
            //             ] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮，所有menu项见附录3
            //         });
            //
            //     }
            // }, 1500);
            //暂时先用定时器
        }
        ,
        mounted() {
            const parameter = this.$route.query.parameter;
            if (parameter) {
                const p = JSON.parse(decodeURIComponent(parameter));
                Object.assign(this.parameter, p);
                console.log('参数信息：', this.parameter);
                this.getAllImage();
            } else {
                this.tips = "参数错误";
                this.showImg = false;
            }
            //     this.pageMixin_setRequestCompleted(true);
            //     if (this.pageParameter.isInit) {
            //         this.getData();
            //     } else {
            //         setTimeout(() => {
            //             this.draw();
            //         }, 500);
            //     }
            //     setTimeout(() => {
            //     }, 500);
            //
            // }

        }
    }
</script>

<style lang='scss' scoped>
    @import "../../assets/global/style/mixin";

    .view-wrapper {
        background-color: #333333;
        /*background-image: url("../../assets/h5/poster/bgw.png");*/
        background-repeat: no-repeat;
        /*animation: bg 30s linear infinite alternate;*/
        /*transform: translateZ(0);*/
        background-size: cover;
        position: relative;
        padding-bottom: 0;
        overflow: hidden;
        @include animation_bg("../../assets/global/bg.png", .2);

        .qrcode {
            width: 100%;
        }

        .content {
            height: calc(100% - 300px);
            position: relative;

            .in-development {
                font-size: 56px;
                color: #a7a7a7;
                position: absolute;
                top: 50%;
                transform: rotateY(-50%);
                margin-top: -300px;
                width: 100%;
                text-align: center;
            }

            .avatar {
                position: absolute;
                width: 0;
                right: 0;
                z-index: 1;

            }

            #canvas {
                display: none;
                width: 750px;
                height: 1334px;
                margin: 0 auto;
                position: absolute;
                top: 0;
                left: 50%;
                margin-left: -375px;

            }

            .poster-wrapper {
                text-align: center;
                /*top: 50%;*/
                /*left: 50%;*/
                /*width: 100%;*/
                width: 100%;
                height: 100%;
                position: relative;
                overflow: hidden;
                z-index: 1;

                .origin {
                    margin-left: 99999999px;
                    opacity: 0;
                    width: 750px;
                    height: 1334px;
                }

                .poster_output {
                    animation: zoomIn .3s linear;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    margin-left: -320px;
                    margin-top: -569px;
                }

                img {
                    width: 640px;
                    height: 1138px;
                    /*width: 750px;*/
                    /*height: 1334px;*/

                }
            }
        }


        .share-in-app {
            width: 100%;
            height: 260px;
            background-color: #ffffff;
            position: fixed;
            bottom: 0;
            left: 0;
            z-index: 1;

            .option-wrapper {
                display: flex;
                width: 750px;
                margin: 0 auto;
                height: 100%;
                align-items: center;


                .option {
                    font-size: 33px;
                    flex: 1;
                    display: block;

                    .label {
                        color: gray;
                        text-align: center;
                    }

                    .icon {
                        width: 100%;
                        height: 150px;
                        background-size: contain;
                        background-repeat: no-repeat;
                        background-position: center center;
                        margin-bottom: 15px;

                    }

                    .friend {
                        background-image: url("../../assets/h5/poster/Group5.png");
                    }

                    .moment {
                        background-image: url("../../assets/h5/poster/Group4.png");
                    }

                    .scan {
                        background-image: url("../../assets/h5/poster/Group9.png");
                    }
                }

            }

        }

        .share-in-wechat {
            width: 100%;
            height: 260px;
            background-color: transparent;
            position: fixed;
            bottom: 0;
            left: 0;
            color: white;
            text-align: center;
            animation: fadeIn 3s linear;
            z-index: 1;

            .arrow {
                background-color: #ffcd00;
                width: 120px;
                height: 120px;
                background-image: url("../../assets/ca/poster/arraw_up.png");
                background-repeat: no-repeat;
                background-size: 100% 100%;
                border-radius: 50%;
                margin: 0 auto;
                margin-bottom: 50px;
            }

            .tips {
                font-size: 40px;
            }
        }
    }
</style>
